<template>
  <div class="order">
    <van-tabs :sticky="true" v-model="activeName" @change="goodstate" color="#50311A" line-height="2" title-active-color="#50311A">

      <van-tab :title="item.title" :name="item.name" v-for="(item, index) in tabContent" :key="index">
        <div class="order-content">
          <div class="order-items">

            <div class="order-item" v-for="(item, index) in goodscook" :key="index">

              <div class="order-item-title">
                <div class="fl order-no">{{item}}</div>
                <div class="fr" v-if="goodsData[item].status == 2">
                  <span class="confirm fl">已收货</span>
                  <span class="delete fl" @click="removegoods(item)">
                    <van-icon class="delete-icon" name="delete" />
                  </span>
                </div>
                <div class="fr" v-else>
                  <span class="confirm" @click="regoods(item)">确认收货</span>
                </div>
                
              </div>

              <div>
                <div class="items" v-for="(v, i) in goodsData[item].data" :key="i">
                  <div class="clearfix">
                    <div class="fl pro-img">
                      <img class="auto-img" :src="v.smallImg" alt />
                    </div>
                    <div class="fl pro-info">
                      <div class="fl">
                        <div class="names">
                          <div class="zh-name">{{v.name}}</div>
                          <div class="en-name">{{v.enname}}</div>
                        </div>
                        <div class="rule">{{v.rule}}</div>
                      </div>
                      <div class="fr">
                        <div class="price">￥{{v.price}}</div>
                        <div class="count">x{{v.count}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div class="order-total clearfix">
                <div class="order-date fl">{{goodsData[item].date | formatDate('aaaa-BB-cc dd:ee:ff')}}</div>
                <div class="fr clearfix">
                  <span class="fl total-count">共计 {{goodsData[item].count}} 件商品 合计：</span>
                  <span class="fl total-price">￥{{goodsData[item].total}}</span>
                </div>
              </div>
            </div>

          </div>
        </div>
      </van-tab>

    </van-tabs>
  </div>
</template>

<script>
import "../../assets/less/Find.less";
export default {
  data() {
    return {
      tabContent: [
        {
          title: '全部',
          name: '0'
        },
        {
          title: '待收货',
          name: '1'
        },
        {
          title: '已收货',
          name: '2'
        }
      ],
      activeName: '0',
      isRecieve: false,
      goodscook: [],
      goodsData: {},
    };
  },

  created() {
    this.goodsget();
  },

  methods: {
    goodstate(a) {
      this.goodsget();
    },

    goodsget() {
      let tokenString = localStorage.getItem('_Tok');
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'GET',
        url: '/findOrder',
        params: {
          appkey: this.appkey,
          tokenString,
          status: this.activeName
        }
      }).then(result => {
        this.$toast.clear();
        if (result.data.code == 70000) {
          result.data.result.sort((a, b) => {
            return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime();
          })
          let oids = [];
          result.data.result.forEach(v => {
            if (oids.indexOf(v.oid) === -1) {
              oids.push(v.oid);
            }
          })
          this.goodscook = oids;
          let goodsData = {};
          result.data.result.forEach(v => {
            if (!goodsData[v.oid]) {
              goodsData[v.oid] = {
                data: [v],
                status: v.status,
                date: v.createdAt,
                count: v.count,
                total: v.count * v.price
              };
            } else {
              goodsData[v.oid].count += v.count;
              goodsData[v.oid].total += v.count * v.price;
              goodsData[v.oid].data.push(v);
            }
          })
          this.goodsData = goodsData;
        }
      }).catch(err => {
        this.$toast.clear();
      })
    },

    removegoods(oid) {
      let tokenString = localStorage.getItem('_Tok');
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'POST',
        url: '/receive',
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      }).then(result => {
        this.$toast.clear();
        if (result.data.code == 80000) {
          this.goodsData[oid].status = 2;
          if (this.activeName == 1) {
            let index = this.goodscook.indexOf(oid);
            this.goodscook.splice(index, 1);
          }
        }
      }).catch(err => {
        this.$toast.clear();
      })
    },

    regoods(oid) {
      let tokenString = localStorage.getItem('_Tok');
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'POST',
        url: '/removeOrder',
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      }).then(result => {
        this.$toast.clear();
        if (result.data.code == 90000) {
          let index = this.goodscook.indexOf(oid);
          this.goodscook.splice(index, 1);
        }
      }).catch(err => {
        this.$toast.clear();
      })
    }
  }
};
</script>